// Mock des constantes
jest.mock('../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'Débutant', color: '#10B981' },
    '2': { title: 'Élémentaire', color: '#3B82F6' },
    '3': { title: 'Intermédiaire', color: '#8B5CF6' }
  },
  CONTAINER_SAFE_EDGES: { NO_BOTTOM: [] }
}));

describe('Dashboard', () => {
  const mockTheme = {
    colors: {
      background: '#F8FAFC',
      primary: '#3B82F6',
      surface: '#FFFFFF',
      text: '#1F2937'
    }
  };

  const renderDashboard = () => {
    return render(
      <ThemeContext.Provider value={mockTheme}>
        <Dashboard />
      </ThemeContext.Provider>
    );
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('devrait rendre le Dashboard correctement', () => {
    renderDashboard();
    
    expect(screen.getByTestId('container')).toBeTruthy();
    expect(screen.getByTestId('modern-header')).toBeTruthy();
    expect(screen.getByTestId('hero-continue-section')).toBeTruthy();
    expect(screen.getByTestId('quick-actions')).toBeTruthy();
    expect(screen.getByTestId('simple-metrics')).toBeTruthy();
    expect(screen.getByTestId('learning-progress')).toBeTruthy();
    expect(screen.getByTestId('revision-orchestrator')).toBeTruthy();
  });

  it('devrait afficher le niveau correct', () => {
    renderDashboard();
    
    expect(screen.getByTestId('header-level')).toHaveTextContent('1');
    expect(screen.getByTestId('quick-level')).toHaveTextContent('1');
    expect(screen.getByTestId('progress-level')).toHaveTextContent('1');
    expect(screen.getByTestId('revision-level')).toHaveTextContent('1');
  });

  it('devrait afficher la couleur du niveau', () => {
    renderDashboard();
    
    expect(screen.getByTestId('header-color')).toHaveTextContent('#10B981');
    expect(screen.getByTestId('quick-color')).toHaveTextContent('#10B981');
    expect(screen.getByTestId('metrics-color')).toHaveTextContent('#10B981');
  });

  it('devrait afficher la progression', () => {
    renderDashboard();
    
    expect(screen.getByTestId('progress-value')).toHaveTextContent('25');
  });

  it('devrait gérer l\'absence d\'activité récente', () => {
    renderDashboard();
    
    expect(screen.getByTestId('hero-loading')).toHaveTextContent('false');
    expect(screen.getByTestId('hero-activity')).toHaveTextContent('none');
  });
});